body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.table {
  width: 1000px;
}

.table-header {
  display: grid;
  grid-template-columns: repeat(5, 500px);
  overflow: scroll;
  background: #638df1;
  color: white;
}

.table-header .cell {
  border-right: 1px solid #6a6ac5;
  text-align: center;
  height: 50px;
}

.separator {
  padding: 20px;
  text-align: center;
}

.table-body {
  display: grid;
  grid-template-columns: repeat(5, 500px);
  overflow: scroll;
  background: whitesmoke;
}

.table-body .cell {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 50px;
  border-bottom: 1px solid grey;
}



